.stars {
  margin-left: auto;
  text-align: end;

  & .stars--outer {
    display: inline-block;
    position: relative;
    font-family: FontAwesome;

    &::before {
      content: "\f006 \f006 \f006 \f006 \f006";
    }
  }

  & .stars--inner {
    position: absolute;
    top: 0;
    left: 0;
    white-space: nowrap;
    overflow: hidden;
    width: 0;

    &::before {
      content: "\f005 \f005 \f005 \f005 \f005";
      color: black;
    }
  }
}

